
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'

import ReceiveList from '../../components/receive/receiveList'
export default {
components: {
Flexbox,
FlexboxItem,
XHeader,
Datetime,
Group,
ReceiveList
},
data() {
return {
canAdd:true,
OrderID:0,
pageParams: {
customerId: 0,
customerName: "",
beginReturnDate: "",
endReturnDate: "",
page: 1,
total: 10,
count: 10,
},
};
},
mounted(){
this.OrderID = this.$route.query.orderID
this.canAdd = this.$route.query.add==1?false:true
},
methods: {
gotoAddPage(){
this.$router.push({
path:`/addReceive?cid=${this.pageParams.customerId}`
})
},
},
};



